import { useContractRead ,useAccount} from 'wagmi'

function NftItem(props) {

	const { address:ethAddress, isConnecting, isDisconnected } = useAccount()
	
let [datos,setDatos]=useState([])
   
   
const { data:dataRead, isError, isLoading } = useContractRead({
	address: '0x754a08cA73CB14264ACCd1E88c3D17f655405861',
	abi: collection,
	args:[ethAddress],
	functionName: 'getTokensURI',
  })
	  useEffect(()=>{

		async function  init(){
			
	console.log("data ")
			let name=""
			let description=""
			let image=""
			
	let newData=
	dataRead.map(async (item)=>{
		await fetch(item)
			.then(function (response) {
	  
	console.log("data "+item)
			  return response.json();
			}).then(function (data) {
				setDatos(datos => [...datos, {
					name: data.name,
					description:data.description,
					image: data.image
				}]);	
			  name = data.name
	  
			  description = data.description
			  image = data.image
			})
	})
	console.log("data "+JSON.stringify(datos))
	

	}
if(dataRead){

	init()
}
}

  ,[dataRead])
    return (
        <div className='page-nft'>
            <PageTitle title='MY NFT ITEM’S' />

            <Project data={datos??[]} />
        </div>
    );
}

